import React from 'react';
import './App.css';
import {connect} from 'react-redux'
import Lists from './list'


class App extends React.Component{
  constructor(props){
    super(props);
    this.state ={
         list: []
         }
  }
   
  componentDidMount(){
    fetch('http://dummy.restapiexample.com/api/v1/employees')
    .then(res=> res.json())
    .then((data)=> {
      this.setState({
        list: data
      });

      this.props.dispatch({
        type:'ADD_POST',
        data});

    })
  }


  render (){
    
    return(
    <div className="App">
       
       <Lists/>
      
     asdd
    </div>
  );
  }
}

export default connect () (App);
