import React from 'react';
import {connect} from 'react-redux'
import SearchResult from 'react-filter-search'

class Lists extends React.Component{
    constructor(props){
        super(props);
        this.state ={ 
            value: ''
        }
    }

    handleChange = (event) =>{
     const {value} = event.target
     this.setState({
         value
     })
    }

  render (){
    const {value} = this.state
    return(
    <div className="App">
       <input type="text" id="inputSearch" placeholder="Search here..." value={value} onChange={this.handleChange} />
        
       {this.props.Posts.map((item) => {
            return (
                <table>

                      <SearchResult value={value} data={item} renderResults ={ results =>(
                         <tbody>
                             <tr>
                                 <th> ID details</th>
                                 <th> Details</th>
                                 <th> No 1</th>
                                 <th> No 2</th>
                             </tr>
                             {results.map((itm)=> 
                        <tr>
                            <td> {itm.id}</td>
                            <td> {itm.employee_salary}</td>
                            <td> {itm.employee_name}</td>
                            <td> {itm.employee_age}</td>
                            
                        </tr>
                    
                       )}
                         </tbody>
                     )}
                     
                     />
                   
                   
                    
               </table>
            );
        })}
    
      
     
    </div>
  );
  }
}

const mapStateToProps = (state) => {
    return {
    Posts: state
    }
   }
export default connect (mapStateToProps) (Lists);
